*{
  font-family: 'Work Sans', sans-serif;
}
html, body{
  height: 100%;
  color: #1a1b1c;
  background-color: #181818;
}

label, a{
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  display: inline-block;
  color: inherit;
  transition: border 0.2s;
  border-bottom: 5px solid rgba(black, 0.2);
  padding: 3px 2px;
  &:hover{
    border-bottom-color: red;
  }
}

.layout{
  display: grid;
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin-top: 50px;
  grid-template-rows: 55px 1fr;
}

input[type="radio"]{
  display: none;

}

label.nav{
  display: flex;
  width: 270px;
  margin-left: 22px;
  margin-bottom: 15px;
  transform: skew(-10deg); 
  -webkit-transform: skew(-10deg);
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #ffffff;
  border:#ffffff 1px solid;
  background: black;
  user-select: none;
  transition: background 0.4s, padding-left 0.2s;
  padding-left: 0;
  input[type="radio"]:checked + .page + &{
    background: red;
    border: none;
    color: #ffffff;
    
   
  }
  
  
}

.page{
  grid-column-start: 1;
  grid-row-start: 2;
  grid-column-end: span 3;
  padding: 0px 20px;
  display: flex;
  align-items: center;
}

.page-contents > *{
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.2s, transform 0.2s;
  @for $c from 1 through 20{
    &:nth-child(#{$c}){
      transition-delay: 0.2s + ($c * 0.2s);
    }
  }
}

input[type="radio"] + .page{
  transition: transform 0.2s;
  transform: translateX(100%);
}

input[type="radio"]:checked + .page{
  transform: translateX(0%);
  .page-contents > *{
    opacity: 1;
    transform: translateY(0px);
  }
}

.page-contents{
  max-width: 100%;
  width: 850px;
  margin: 0 auto;
}